import STATE_core from '@/store/STATE_core.js';

/**
 * Constants
*/
class Constants
{
	/**
	 * email_address
	*/
	email_address()
	{
		if (STATE_core().is_partners_skin()) {
			return 'poistenie@partnersgroup.sk';
		}
		if (STATE_core().is_finportal_skin()) {
			return 'finportal@finportal.sk';
		}
		return 'najpoistenie@najpoistenie.sk';
	}


	/**
	 * phone_number
	*/
	phone_number(tech_support)
	{
		if (tech_support) {
			// technical support for agents
			return '+421 905 497 554';
		}
		// common public users
		return '+421 917 369 449';
	}


	/**
	 * group_names
	*/
	group_names()
	{
		return {
			1: 'Povinn zmluvn poistenie',
			2: 'Havarijn poistenie',
			3: 'Hypotekrny ver',
			4: 'ivotn poistenie',
			5: 'GAP poistenie finannej straty',
			6: 'Investcie',
			7: 'Cestovn poistenie',
			8: 'Poistenie zodpovednosti zamestnanca',
			9: 'Poistenie majetku - byt, dom, domcnos',
			10: 'Poistenie podnikateov',
			72: 'Sedadl',
			73: 'Zdravotn poistenie cudzincov',
			81: 'Zdravotn poistenie',
			82: 'Test',
			85: 'Starobn dchodkov sporenie', // dss
			86: 'Doplnkov dchodkov sporenie', // dds
		};
	}


	/**
	 * group_name
	*/
	group_name(id)
	{
		return this.group_names()[id] || 'Neidentifikovan grupa';
	}


	/**
	 * step_names
	*/
	step_names(int)
	{
		let steps = {
			'-1': 'Cookie manager',
			0: 'Preposlan ponuka',
			1: 'Vstupn formulr',
			2: 'Vber produktov',
			3: 'daje klienta',
			4: 'Nvrh zmluvy',
			5: 'Potvrdenie zmluvy',
			6: 'Nvrat z platby',
			88: 'Testovac dizajn',
			99: 'Testovanie logiky',
			111: 'Cookie manager',
			404: 'Error strnka',
			503: 'drba',
			'-77': 'istenie session',
		};
		return steps[int];
	}


	/**
	 * receiver_names
	*/
	receiver_names(int)
	{
		let receivers = {
			1: 'Finportal',
			2: 'Najpoistenie',
			3: 'UMS',
			4: 'Partners group',
			5: 'Centrum poistenia',
			6: 'Realfin',
		};
		return receivers[int];
	}
}

export default new Constants;
