<template>
	<Modal_body :close="false" :title="product_name" class="" :huge="true" >

		<!-- top nav -->
		<div class="row pb-3 advanced_settings_nav">
			<div class="col-6">
				<button
					v-for="(tab_number, index) in tab_count"
					@click="switch_the_tab(tab_number)"
					class="btn btn-sm btn-secondary me-5"
					:class="current_person === tab_number ? 'active' : 'faded'"
				>
					{{ index + 1 }}. poisten
				</button>
			</div>
			<div class="col-3 text-end">
				<button
					class="btn btn-sm btn-secondary btn-left"
					@click="return_to_step1"
				>
					{{ first_person ? 'Nvrat na vstup' : 'Nasp' }}
				</button>
			</div>
			<div class="col-3 text-end">
				<Bouncer_loading
					v-if="loading"
					style="margin-top: 10px;"
				/>
				<button
					v-else
					class="btn btn-sm btn-primary btn-right"
					@click="validate_extended"
				>
					{{ last_person ? 'Prepota produkt' : 'Pokraova' }}
				</button>
			</div>
		</div>

		<!-- tabs -->
		<Tabs />

		<!-- bottom nav -->
		<div class="row mt-4">
			<div class="col-9 text-end">
				<button
					class="btn btn-sm btn-secondary btn-left"
					@click="return_to_step1"
				>
					{{ first_person ? 'Nvrat na vstup' : 'Nasp' }}
				</button>
			</div>
			<div class="col-3 text-end">
				<Bouncer_loading
					v-if="loading"
					style="margin-top: 10px;"
				/>
				<button
					v-else
					class="btn btn-sm btn-primary btn-right"
					@click="validate_extended"
				>
					{{ last_person ? 'Prepota produkt' : 'Pokraova' }}
				</button>
			</div>
		</div>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_forms from '@/store/STATE_forms.js';
	import STATE_core from '@/store/STATE_core.js';
	import Functions from '@/logic/Functions.js';
	import Forms_extended from '@/logic/requests/Forms_extended.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Tabs from '@/components/forms/Tabs.vue';
	import STATE_nav from '@/store/STATE_nav.js';

	export default {
		props: {
		},
		components: {
			Bouncer_loading,
			Modal_body,
			Tabs,
		},
		data() {
			return {
				extended_inputs: {},
				extended_errors: null, // null | {}
				product_name: '',
				product_id: 0,
				period: 0,
				loading: false,
			}
		},
		created() {
			// useful data
			this.extended_inputs = STATE_modals().retrieve_modal_data().extended_inputs;
			this.extended_errors = STATE_modals().retrieve_modal_data().extended_errors;
			this.product_name = STATE_modals().retrieve_modal_data().product_name;
			this.product_id = STATE_modals().retrieve_modal_data().product_id;
			this.period = STATE_modals().retrieve_modal_data().period;

			// prepare form values
			STATE_params().insert_product_values(this.extended_inputs);

			// prepare form state
			STATE_forms().set_form(this.extended_inputs);
			if (this.extended_errors !== null) {
				STATE_forms().set_errors(structuredClone(this.extended_errors));
			}

			// set tabs
			STATE_nav().set_tab_keys(this.extended_inputs);
		},
		methods: {
			// broken !DRY! with step1
			return_to_step1()
			{
				// first tab redirect to step1
				if (this.first_person)
				{
					STATE_forms().clear_form();
					STATE_params().reset_form_values();
					STATE_forms().clear_errors();
					STATE_nav().reset_tab_keys();
					STATE_core().change_step(1);
				}

				// further tabs switch to previous
				else
				{
					STATE_nav().decrease_tab();
					if (Functions.this_tab_is_empty()) {
						// repeat function if hit empty tab
						this.return_to_step1();
					}
				}
			},
			// broken !DRY! with step1
			validate_extended()
			{
				// loading
				this.loading = true;

				// validate form
				Forms_extended.validate_extended_form().then(
					(response) => {

						// loading
						this.loading = false;

						// error
						if (response.status !== 200) {
							return;
						}

						// reset errors
						STATE_forms().clear_errors();

						// after successful validate
						this.process_errors_and_go_further(response);

					}
				);
			},
			// broken !DRY! with step1
			process_errors_and_go_further(response)
			{
				// last tab validates whole step
				if (STATE_nav().current_tab_is_last())
				{

					// valid, reset state, close modal, let step2 run calculate
					if (response.result.validform === true)
					{
						// probably should reset values and form same way when we change step
						STATE_forms().clear_form();
						STATE_params().reset_form_values();
						STATE_nav().reset_tab_keys();

						// initiate recalculate when closing modal
						STATE_modals().set_modal_event_data(this.product_id);

						// close modal
						STATE_modals().close_them_all();
					}

					// invalid, show first tab with error
					else
					{
						STATE_forms().set_errors(response.result.form_validation_error_elements);
						STATE_nav().open_first_tab_with_error(response.result.form_validation_error_tab_elements);
						Functions.adjust_view(true);
					}

				}

				// tabs before validates only themself
				else
				{

					let error_element = response.result.form_validation_error_tab_elements?.[ STATE_nav().get_current_tab() ];

					// valid, go next tab
					if (error_element === undefined)
					{
						STATE_nav().increase_tab();
						if (Functions.this_tab_is_empty()) {
							// repeat function if hit empty tab
							this.process_errors_and_go_further(response);
						}
						Functions.adjust_view();
					}

					// invalid show errors for this tab
					else
					{
						STATE_forms().set_errors(error_element);
						Functions.adjust_view(true);
					}

				}
			},
			switch_the_tab(tab_number)
			{
				STATE_nav().set_current_tab(tab_number);
			},
		},
		computed: {
			tab_count()
			{
				return STATE_nav().get_tab_keys();
			},
			first_person()
			{
				return STATE_nav().current_tab_is_first();
			},
			last_person()
			{
				return STATE_nav().current_tab_is_last();
			},
			current_person()
			{
				return STATE_nav().get_current_tab();
			},
		},
	}
</script>

<style lang="css">
	/* this somehow uses 12.5px in NP web, but 14px in wrapper
	.osm_app_scope[data-v-7ba5bd90] .btn {
		font-size: var(--bs-btn-font-size);
	}*/
	.advanced_settings_nav {
		.btn-sm {
			font-size: 14px;
		}
		.faded {
			opacity: 0.5;
		}
	}
</style>
