import { defineStore } from 'pinia';
import Functions from '@/logic/Functions.js';

export default defineStore(
	'STATE_nav',
	{
		state: () => {
			return {
				requested_direction: null, // null | string 'backward' | string 'forward'
				tab_current: 0, // int
				tab_first: 0, // int
				tab_last: 0, // int
				tab_keys: [], // array of integers
			};
		},
		getters: {

			// direction
			get_requested_direction()
			{
				return () => {
					return this.requested_direction;
				}
			},

			// tab_current
			get_current_tab()
			{
				return () => {
					return this.tab_current;
				}
			},
			current_tab_is_first()
			{
				return () => {
					return this.tab_current === this.tab_first;
				}
			},
			current_tab_is_last()
			{
				return () => {
					return this.tab_current === this.tab_last;
				}
			},

			// tab_keys
			get_tab_keys()
			{
				return () => {
					return this.tab_keys;
				}
			},

		},
		actions: {

			// direction
			set_requested_direction(direction)
			{
				this.requested_direction = direction;
			},
			reset_requested_direction()
			{
				this.requested_direction = null;
			},

			// tab_current
			set_current_tab(int)
			{
				this.tab_current = parseInt(int, 10);
			},
			decrease_tab()
			{
				this.tab_current--;
			},
			increase_tab()
			{
				this.tab_current++;
			},
			open_first_tab_with_error(errors)
			{
				let first_tab = Functions.first_key(errors);
				this.tab_current = parseInt(first_tab, 10);
			},

			// tab_keys
			set_tab_keys(form)
			{
				if (form === undefined || typeof form !== 'object') {
					return;
				}

				let form_keys = Object.keys(form);
				this.tab_keys = [];

				for (let index = 0; index < form_keys.length; index++)
				{
					let key = parseInt(form_keys[index], 10);
					this.tab_keys.push(key);
				}

				this.tab_first = this.tab_keys[0];
				this.tab_last = this.tab_keys[ this.tab_keys.length - 1 ];
				this.tab_current = this.tab_first;
			},
			reset_tab_keys()
			{
				this.tab_current = 0;
				this.tab_first = 0;
				this.tab_last = 0;
				this.tab_keys = [];
			},

		},
	}
);
