<template>
	<section data-cy="calc_forms" >

		<Zeteo_nav
			v-if="!classic_np_nav"
			@prev="prev_button"
			@next="next_button"
			:show_back="show_back"
		/>

		<Tabs />

		<Navigation
			v-if="classic_np_nav"
			@prev="prev_button"
			@next="next_button"
			:show_back="show_back"
			:show_next="agent_clearance_ok"
			:disable_next="false"
		/>

	</section>
</template>

<script>
	import Navigation from '@/components/form_app/nav/Navigation.vue';
	import Zeteo_nav from '@/components/form_app/nav/Zeteo_nav.vue';
	import Forms_group from '@/logic/requests/Forms_group.js';
	import Forms_user from '@/logic/requests/Forms_user.js';
	import Functions from '@/logic/Functions.js';
	import Tabs from '@/components/forms/Tabs.vue';
	import STATE_forms from '@/store/STATE_forms.js';
	import STATE_core from '@/store/STATE_core.js';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Form_cache from '@/logic/Form_cache.js';
	import STATE_nav from '@/store/STATE_nav.js';

	export default {
		props: {
		},
		components: {
			Navigation,
			Tabs,
			Zeteo_nav,
		},
		data() {
			return {
				is_step: 1,
				is_fresh_session: false,
				agent_clearance_ok: true,
			}
		},
		created() {

			// loading
			STATE_modals().open_it('Loading');

			// 1 or 3
			this.is_step = STATE_core().current_step();

			// if empty session_token before first getgroupform, it means its fresh
			if (STATE_core().is_fresh_session()) {
				this.is_fresh_session = true;
			}

			// get the form on load
			Promise.resolve().then(
				() => {
					if (this.is_fresh_session) {
						return Promise.resolve();
					}
					// getsessioninfo
					return Session_data.session_info();
				}
			).then(
				(response) => {
					if (this.is_fresh_session) {
						return Promise.resolve();
					}
					// check if session is confirmed
					if (response.result?.state >= 100) {
						STATE_modals().close_them_all();
						STATE_core().change_step(5);
						return Promise.reject('session already confirmed');
					}
				}
			).then(
				() => {
					// reopening step3 is missing selected_product
					if (this.is_step === 3 && STATE_params().get_selected_product().form_id === undefined) {
						return Session_data.get_data();
					}
					// step1 goes immidiatelly forward
					else {
						return Promise.resolve();
					}
				}
			).then(
				(response) => {
					// only step3 had response
					if (response !== undefined) {
						// save selected product
						STATE_params().extract_selected_product(response);
					}
				}
			).then(
				() => {
					// getgroupform or getform
					if (this.is_step === 1) {
						return Forms_group.get_group_form();
					} else if (this.is_step === 3) {
						return Forms_user.get_user_form();
					}
				}
			).then(
				(response) => {

					// agent clearance failed in getgroupform or getform
					if (response.status === 423) {
						this.agent_clearance_ok = false;
					}

					// error
					if (response.status !== 200) {
						return;
					}

					// close loading
					STATE_modals().close_them_all();

					// save session token if fresh session
					if (this.is_fresh_session) {
						STATE_core().set_session_token(response.result.token);
					}

					// save call_params for hide-condition
					STATE_params().store_call_params(response.result.call_params);

					// save form
					let form = response.result.form;
					STATE_forms().set_form(form);
					STATE_params().insert_form_values(form);

					// set initial tabs
					STATE_nav().set_tab_keys(form);

					// send built html to backend
					Form_cache.store_form_cache(this.is_fresh_session);

					// temporary helper, inform backend that this session was created in SPA
					if (this.is_fresh_session) {
						Session_data.set_data({osm_author_spa: 1,});
					}

				}
			).catch((error) => { console.log(error); });
		},
		methods: {
			prev_button()
			{
				// from last tab, delete state and go to step2
				if (STATE_nav().current_tab_is_first())
				{
					STATE_forms().clear_form();
					STATE_params().reset_form_values();
					STATE_nav().reset_tab_keys();
					STATE_core().change_step(2);
				}

				// or just show previous tab
				else
				{
					STATE_nav().decrease_tab();
					if (Functions.this_tab_is_empty()) {
						// repeat function if hit empty tab
						this.prev_button();
					}
					Functions.adjust_view();
				}
			},
			next_button()
			{
				// loading
				STATE_modals().open_it('Loading');

				// validategroupform or validateform
				Promise.resolve().then(
					() => {
						if (this.is_step === 1) {
							return Forms_group.validate_group_form();
						} else if (this.is_step === 3) {
							return Forms_user.validate_user_form();
						}
					}
				).then(
					(response) => {

						// close loading
						STATE_modals().close_them_all();

						// error
						if (response.status !== 200) {
							return;
						}

						// reset errors
						STATE_forms().clear_errors();

						// after successful validate
						this.check_errors_go_next(response);

					}
				);
			},
			check_errors_go_next(response)
			{
				// last tab validates whole step
				if (STATE_nav().current_tab_is_last())
				{

					// valid, reset state and go next step
					if (response.result.validform === true)
					{
						STATE_forms().clear_form();
						STATE_params().reset_form_values();
						STATE_nav().reset_tab_keys();
						STATE_core().change_step(this.is_step + 1);
					}

					// invalid, show first tab with error
					else
					{
						STATE_forms().set_errors(response.result.form_validation_error_elements);
						STATE_nav().open_first_tab_with_error(response.result.form_validation_error_tab_elements);
						Functions.adjust_view(true);
					}

				}

				// tabs before validates only themself
				else
				{

					let error_element = response.result.form_validation_error_tab_elements?.[ STATE_nav().get_current_tab() ];

					// valid, go next tab
					if (error_element === undefined)
					{
						STATE_nav().increase_tab();
						if (Functions.this_tab_is_empty()) {
							// repeat function if hit empty tab
							this.check_errors_go_next(response);
						}
						Functions.adjust_view();
					}

					// invalid show errors for this tab
					else
					{
						STATE_forms().set_errors(error_element);
						Functions.adjust_view(true);
					}

				}
			},
		},
		computed: {
			show_back()
			{
				// show back button only when needed
				return (this.is_step === 3 || !STATE_nav().current_tab_is_first()) && this.agent_clearance_ok;
			},
			classic_np_nav()
			{
				return !STATE_core().is_zeteo_skin();
			},
			// watched
			requested_direction()
			{
				return STATE_nav().get_requested_direction();
			},
		},
		watch: {
			requested_direction()
			{
				if (this.requested_direction === null) {
					return;
				}
				if (this.requested_direction === 'backward') {
					STATE_nav().reset_requested_direction();
					this.prev_button();
				} else if (this.requested_direction === 'forward') {
					STATE_nav().reset_requested_direction();
					this.next_button();
				}
			},
		},
	}
</script>
