import STATE_params from '@/store/STATE_params.js';

/**
 * Associative
*/
class Associative
{
	/**
	 * init_associative
	*/
	init_associative(field_id, field_data)
	{
		if (field_data.input_settings?.associative === undefined) {
			return;
		}
		let settings = structuredClone(field_data.input_settings?.associative);
		/* example: [
			{
				if: '$zp_osoba1_ps_kch <= $zp_osoba1_ps_kch_1',
				set: '$zp_osoba1_ps_kch',
				targets: ['zp_osoba1_ps_kch_1',],
			},
		]*/

		let required_keys = ['if', 'set', 'targets',];
		for (let index in required_keys) {
			for (let order in settings) {
				if (Object.keys(settings[order]).indexOf(required_keys[index]) === -1) {
					console.log('associative key is missing:', required_keys[index]);
					return;
				}
			}
		}

		console.log('evaluating associative:', field_id);

		// could have multiple conditions
		for (let index in settings) {

			// if
			let string = this.replace_params(settings[index].if);
			console.log('condition:', string);
			if (!this.eval_associative(string)) {
				console.log(false);
				continue;
			}
			console.log(true);

			// value
			let value = this.replace_params(settings[index].set);
			value = this.eval_associative(value);
			console.log('value:', value);

			// set value, could be setting multiple values
			for (let order in settings[index].targets) {
				console.log('setting value to:', settings[index].targets[order]);
				STATE_params().set_form_value(settings[index].targets[order], value);
			}

		}
	}

	/**
	 * replace_params
	*/
	replace_params(string)
	{
		let match = string.match(/\$[a-z|_|0-9]*/g);
		if (match === null) {
			return string;
		}
		for (let index in match) {
			let id = match[index].replace('$', '');
			let value = STATE_params().get_user_input(id) || '';
			if (isNaN(value) || value === '') {
				value = "'" + value + "'";
			}
			string = string.replace(match[index], value);
		}
		return string;
	}

	/**
	 * eval_associative
	*/
	eval_associative(string)
	{
		try {
			return eval(string);
		} catch (error) {
			console.log('associative error:', string);
			return false;
		}
	}
}

export default new Associative;
